import React, { useState } from 'react';
import type { CustomQuestionSet, CustomWord } from '../types/customQuestions';

interface FloatingPanelProps {
  /** / */
  isOpen: boolean;
  /**  */
  onClose: () => void;
  /**  */
  sets: CustomQuestionSet[];
  /**  */
  onCreateSet: (name: string, description?: string) => void;
  /**  */
  onDeleteSet: (setId: string) => void;
  /**  */
  onEditSet: (setId: string, name: string, description?: string) => void;
  /**  */
  onRemoveWord: (setId: string, word: CustomWord) => void;
}

/**
 * 
 */
const FloatingPanel: React.FC<FloatingPanelProps> = ({
  isOpen,
  onClose,
  sets,
  onCreateSet,
  onDeleteSet,
  onEditSet,
  onRemoveWord,
}) => {
  const [activeTab, setActiveTab] = useState<'manage' | 'create'>('manage');
  const [newSetName, setNewSetName] = useState('');
  const [newSetDescription, setNewSetDescription] = useState('');
  const [expandedSetId, setExpandedSetId] = useState<string | null>(null);
  const [editingSetId, setEditingSetId] = useState<string | null>(null);
  const [editName, setEditName] = useState('');
  const [editDescription, setEditDescription] = useState('');

  if (!isOpen) return null;

  const handleCreateSet = () => {
    if (newSetName.trim()) {
      onCreateSet(newSetName.trim(), newSetDescription.trim() || undefined);
      setNewSetName('');
      setNewSetDescription('');
      setActiveTab('manage');
    }
  };

  const handleStartEdit = (set: CustomQuestionSet) => {
    setEditingSetId(set.id);
    setEditName(set.name);
    setEditDescription(set.description || '');
  };

  const handleSaveEdit = () => {
    if (editingSetId && editName.trim()) {
      onEditSet(editingSetId, editName.trim(), editDescription.trim() || undefined);
      setEditingSetId(null);
    }
  };

  const handleCancelEdit = () => {
    setEditingSetId(null);
    setEditName('');
    setEditDescription('');
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-2 sm:p-4">
      <div className="bg-white rounded-lg shadow-2xl max-w-2xl sm:max-w-4xl w-full mx-auto max-h-[90vh] flex flex-col">
        {/*  */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <h2 className="text-2xl font-bold text-gray-900"> </h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700:text-gray-200 text-2xl"
            aria-label=""
          >
            
          </button>
        </div>

        {/*  */}
        <div className="flex border-b border-gray-200">
          <button
            onClick={() => setActiveTab('manage')}
            className={`flex-1 px-6 py-3 font-medium ${
              activeTab === 'manage'
                ? 'text-blue-600 border-b-2 border-blue-600'
                : 'text-gray-600 hover:text-gray-900:text-gray-200'
            }`}
          >
              ({sets.length})
          </button>
          <button
            onClick={() => setActiveTab('create')}
            className={`flex-1 px-6 py-3 font-medium ${
              activeTab === 'create'
                ? 'text-blue-600 border-b-2 border-blue-600'
                : 'text-gray-600 hover:text-gray-900:text-gray-200'
            }`}
          >
             
          </button>
        </div>

        {/*  */}
        <div className="flex-1 overflow-y-auto p-6">
          {activeTab === 'manage' ? (
            <div className="space-y-4">
              {sets.length === 0 ? (
                <div className="text-center py-12 text-gray-500">
                  <p className="text-lg mb-2"> </p>
                  <p className="text-sm"></p>
                </div>
              ) : (
                sets.map((set) => (
                  <div key={set.id} className="border border-gray-200 rounded-lg overflow-hidden">
                    {/*  */}
                    <div className="bg-gray-50 p-4">
                      {editingSetId === set.id ? (
                        <div className="space-y-2">
                          <input
                            type="text"
                            value={editName}
                            onChange={(e) => setEditName(e.target.value)}
                            className="w-full px-3 py-2 border border-gray-300 rounded-md bg-white text-gray-900"
                            placeholder=""
                          />
                          <textarea
                            value={editDescription}
                            onChange={(e) => setEditDescription(e.target.value)}
                            className="w-full px-3 py-2 border border-gray-300 rounded-md bg-white text-gray-900"
                            placeholder=""
                            rows={2}
                          />
                          <div className="flex gap-2">
                            <button
                              onClick={handleSaveEdit}
                              className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600"
                            >
                              
                            </button>
                            <button
                              onClick={handleCancelEdit}
                              className="px-4 py-2 bg-gray-300 text-gray-900 rounded-md hover:bg-gray-400:bg-gray-500"
                            >
                              
                            </button>
                          </div>
                        </div>
                      ) : (
                        <div className="flex items-start justify-between">
                          <div className="flex-1">
                            <div className="flex items-center gap-2">
                              <span className="text-2xl">{set.icon || ''}</span>
                              <h3 className="text-lg font-bold text-gray-900">{set.name}</h3>
                              <span className="text-sm text-gray-500">({set.words.length})</span>
                            </div>
                            {set.description && (
                              <p className="text-sm text-gray-600 mt-1">{set.description}</p>
                            )}
                            <p className="text-xs text-gray-500 mt-1">
                              : {new Date(set.createdAt).toLocaleDateString('ja-JP')}
                            </p>
                          </div>
                          <div className="flex gap-2">
                            <button
                              onClick={() => handleStartEdit(set)}
                              className="text-blue-600 hover:text-blue-700:text-blue-300 text-sm"
                              aria-label=""
                            >
                              
                            </button>
                            <button
                              onClick={() =>
                                setExpandedSetId(expandedSetId === set.id ? null : set.id)
                              }
                              className="text-gray-600 hover:text-gray-700:text-gray-300 text-sm"
                              aria-label={expandedSetId === set.id ? '' : ''}
                            >
                              {expandedSetId === set.id ? '' : ''}
                            </button>
                            <button
                              onClick={() => {
                                if (confirm(`${set.name}`)) {
                                  onDeleteSet(set.id);
                                }
                              }}
                              className="text-red-600 hover:text-red-700:text-red-300 text-sm"
                              aria-label=""
                            >
                              
                            </button>
                          </div>
                        </div>
                      )}
                    </div>

                    {/*  */}
                    {expandedSetId === set.id && (
                      <div className="p-4 bg-white">
                        {set.words.length === 0 ? (
                          <p className="text-sm text-gray-500 text-center py-4">
                            
                          </p>
                        ) : (
                          <div className="space-y-2">
                            {set.words.map((word, index) => (
                              <div
                                key={`${word.word}-${index}`}
                                className="flex items-center justify-between p-3 bg-gray-50 rounded-md"
                              >
                                <div className="flex-1">
                                  <div className="flex items-center gap-2">
                                    <span className="font-semibold text-gray-900">{word.word}</span>
                                    {word.ipa && (
                                      <span className="text-sm text-blue-600">/{word.ipa}/</span>
                                    )}
                                  </div>
                                  <p className="text-sm text-gray-700 mt-1">{word.meaning}</p>
                                  {word.source && (
                                    <p className="text-xs text-gray-500 mt-1">
                                      : {word.source}
                                      {word.sourceDetail && ` (${word.sourceDetail})`}
                                    </p>
                                  )}
                                </div>
                                <button
                                  onClick={() => onRemoveWord(set.id, word)}
                                  className="ml-4 text-red-600 hover:text-red-700:text-red-300"
                                  aria-label=""
                                >
                                  
                                </button>
                              </div>
                            ))}
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                ))
              )}
            </div>
          ) : (
            <div className="max-w-2xl mx-auto">
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    value={newSetName}
                    onChange={(e) => setNewSetName(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-md bg-white text-gray-900"
                    placeholder=": "
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <textarea
                    value={newSetDescription}
                    onChange={(e) => setNewSetDescription(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-md bg-white text-gray-900"
                    placeholder=": "
                    rows={3}
                  />
                </div>
                <button
                  onClick={handleCreateSet}
                  disabled={!newSetName.trim()}
                  className="w-full px-6 py-3 bg-blue-500 text-white rounded-md hover:bg-blue-600 disabled:bg-gray-300:bg-gray-600 disabled:cursor-not-allowed font-medium"
                >
                   
                </button>
              </div>

              <div className="mt-8 p-4 bg-blue-50 rounded-lg">
                <h4 className="font-medium text-blue-900 mb-2"> </h4>
                <ul className="text-sm text-blue-800 space-y-1">
                  <li> +</li>
                  <li> </li>
                  <li> </li>
                </ul>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default FloatingPanel;
