/**
 * 
 *
 * :
 * - production: 
 * - development: 
 * - debug: URL  ?debug=true 
 */

const isDevelopment = import.meta.env.DEV;
const isDebugMode =
  typeof window !== 'undefined' &&
  new URLSearchParams(window.location.search).get('debug') === 'true';

export const logger = {
  log: (...args: unknown[]) => {
    // 
    if (isDebugMode) {
      console.log(...args);
    }
  },

  warn: (...args: unknown[]) => {
    // 
    if (isDevelopment || isDebugMode) {
      console.warn(...args);
    }
  },

  error: (...args: unknown[]) => {
    // 
    console.error(...args);
  },

  debug: (...args: unknown[]) => {
    // 
    if (isDebugMode) {
      console.debug(...args);
    }
  },

  info: (...args: unknown[]) => {
    // 
    if (isDebugMode) {
      console.info(...args);
    }
  },
};
