import { useState, useEffect } from 'react';
import type { Question, AIPersonality } from '../types';
import type { DataSource } from '../App';
import type { CustomQuestionSet } from '../types/customQuestions';
import LearningPlanView from './LearningPlanView';
import { PERSONALITY_INFO } from '../aiCommentGenerator';

interface SettingsViewProps {
  allQuestions: Question[];
  onStartSession: (mode: 'morning' | 'afternoon' | 'evening', questions: Question[]) => void;
  _selectedDataSource?: DataSource;
  _onDataSourceChange?: (source: DataSource) => void;
  customQuestionSets: CustomQuestionSet[];
  onOpenCustomSetManagement: () => void;
}

function SettingsView({
  allQuestions,
  onStartSession,
  _selectedDataSource = 'all',
  _onDataSourceChange,
  customQuestionSets,
  onOpenCustomSetManagement,
}: SettingsViewProps) {
  // localStorage
  const [batchSize, setBatchSize] = useState<number>(() => {
    const saved = localStorage.getItem('batchSize');
    return saved ? parseInt(saved, 10) : 20;
  });

  // AI
  const [aiPersonality, setAIPersonality] = useState<AIPersonality>(() => {
    const saved = localStorage.getItem('aiPersonality');
    return (saved as AIPersonality) || 'kind-teacher';
  });

  // localStorage
  const handleBatchSizeChange = (newSize: number) => {
    setBatchSize(newSize);
    localStorage.setItem('batchSize', newSize.toString());
  };

  // 
  void handleBatchSizeChange; // ESLint

  // AIlocalStorage
  const handlePersonalityChange = (personality: AIPersonality) => {
    setAIPersonality(personality);
    localStorage.setItem('aiPersonality', personality);
  };

  // 
  const [voiceGender, setVoiceGender] = useState<'female' | 'male'>(() => {
    const saved = localStorage.getItem('voiceGender');
    return saved === 'female' || saved === 'male' ? saved : 'female'; // 
  });

  const [speechRate, setSpeechRate] = useState<number>(() => {
    const saved = localStorage.getItem('speechRate');
    return saved ? parseFloat(saved) : 0.9; // 
  });

  // localStorage
  const handleVoiceGenderChange = (gender: 'female' | 'male') => {
    setVoiceGender(gender);
    localStorage.setItem('voiceGender', gender);
  };

  // localStorage
  const handleSpeechRateChange = (rate: number) => {
    setSpeechRate(rate);
    localStorage.setItem('speechRate', rate.toString());
  };

  // 
  const [darkMode, setDarkMode] = useState<'light' | 'dark' | 'system'>(() => {
    const saved = localStorage.getItem('darkMode');
    if (saved === 'system' || saved === 'light' || saved === 'dark') {
      return saved;
    }
    // boolean
    if (saved === 'true') return 'dark';
    if (saved === 'false') return 'light';
    return 'system';
  });

  // Tailwinddark
  const applyDarkMode = (mode: 'light' | 'dark' | 'system') => {
    let isDark = false;
    if (mode === 'system') {
      isDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    } else {
      isDark = mode === 'dark';
    }
    // Tailwinddark
    document.documentElement.classList.toggle('dark', isDark);
  };

  // localStorage
  const _handleDarkModeChange = (mode: 'light' | 'dark' | 'system') => {
    setDarkMode(mode);
    localStorage.setItem('darkMode', mode);
    applyDarkMode(mode);
  };

  // 
  useEffect(() => {
    // main.tsx
    applyDarkMode(darkMode);

    // system
    if (darkMode === 'system') {
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
      const handleChange = () => {
        applyDarkMode('system');
      };
      mediaQuery.addEventListener('change', handleChange);
      return () => mediaQuery.removeEventListener('change', handleChange);
    }
  }, [darkMode]);

  const _totalWords = allQuestions.length;
  const estimatedDays = Math.ceil(_totalWords / batchSize);
  const _estimatedDaysText = `${estimatedDays}`;

  return (
    <div className="w-full px-4 py-6 space-y-6">
      {/*  */}
      <div className="bg-card-bg rounded-xl p-6 shadow-md border-2 border-card-border">
        <h3 className="text-xl font-bold text-text-color mb-4 flex items-center gap-2">
          <span></span>
          <span></span>
          <span className="text-sm font-normal text-text-secondary">
            ({customQuestionSets.length})
          </span>
        </h3>
        <div className="space-y-3">
          <button
            onClick={onOpenCustomSetManagement}
            className="w-full px-6 py-4 bg-gradient-to-r from-purple-500 to-pink-500 text-white text-center rounded-lg font-semibold hover:from-purple-600 hover:to-pink-600 transition-all shadow-md flex items-center justify-center gap-2"
          >
            <span></span>
            <span></span>
          </button>
          <p className="text-sm text-text-secondary bg-bg-secondary px-4 py-3 rounded-lg">
            
            
          </p>
        </div>
      </div>

      {/*  */}
      <LearningPlanView allQuestions={allQuestions} onStartSession={onStartSession} />

      {/* AI */}
      <div className="bg-card-bg rounded-xl p-6 shadow-md border-2 border-card-border">
        <h3 className="text-xl font-bold text-text-color mb-4 flex items-center gap-2">
          <span></span>
          <span>AI</span>
        </h3>
        <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
          {(
            Object.entries(PERSONALITY_INFO) as [
              AIPersonality,
              (typeof PERSONALITY_INFO)[AIPersonality],
            ][]
          ).map(([key, info]) => (
            <button
              key={key}
              className={`flex flex-col items-center gap-2 p-4 rounded-lg border-2 transition-all duration-200 ${
                aiPersonality === key
                  ? 'bg-primary border-primary text-white shadow-lg scale-105'
                  : 'bg-bg-secondary border-border-color text-text-color hover:border-primary hover:shadow-md hover:scale-102'
              }`}
              onClick={() => handlePersonalityChange(key)}
            >
              <div className="text-3xl">{info.avatar}</div>
              <div className="font-semibold text-sm">{info.name}</div>
              <div className="text-xs opacity-80 text-center">{info.description}</div>
            </button>
          ))}
        </div>
      </div>

      {/*
         - 
        ---
        
        darkMode state, applyDarkMode, handleDarkModeChange
        src/components/SettingsView.tsx 
        

        :
        - SettingsView.tsx (L71-115): darkMode state & logic
        - App.tsx (L273-287): applyDarkMode logic
        - main.tsx (L8-36): initialization logic
        - src/styles/themes/dark.css: CSS variables (will be used)
      */}

      {/*  */}
      <div className="bg-card-bg rounded-xl p-6 shadow-md border-2 border-card-border">
        <h3 className="text-xl font-bold text-text-color mb-4 flex items-center gap-2">
          <span></span>
          <span></span>
        </h3>

        {/*  */}
        <div className="mb-6">
          <h4 className="text-base font-semibold text-text-color mb-3 flex items-center gap-2">
            <span></span>
            <span></span>
          </h4>
          <div className="grid grid-cols-2 gap-3">
            <button
              className={`flex flex-col items-center gap-2 px-4 py-4 rounded-lg border-2 transition-all duration-200 ${
                voiceGender === 'female'
                  ? 'bg-primary border-primary text-white shadow-lg scale-105'
                  : 'bg-bg-secondary border-border-color text-text-color hover:border-primary hover:shadow-md'
              }`}
              onClick={() => handleVoiceGenderChange('female')}
            >
              <div className="text-3xl"></div>
              <div className="text-sm font-semibold"></div>
            </button>
            <button
              className={`flex flex-col items-center gap-2 px-4 py-4 rounded-lg border-2 transition-all duration-200 ${
                voiceGender === 'male'
                  ? 'bg-primary border-primary text-white shadow-lg scale-105'
                  : 'bg-bg-secondary border-border-color text-text-color hover:border-primary hover:shadow-md'
              }`}
              onClick={() => handleVoiceGenderChange('male')}
            >
              <div className="text-3xl"></div>
              <div className="text-sm font-semibold"></div>
            </button>
          </div>
        </div>

        {/*  */}
        <div>
          <h4 className="text-base font-semibold text-text-color mb-3 flex items-center gap-2">
            <span></span>
            <span></span>
            <span className="text-xs font-normal text-text-secondary">(WPM: Words Per Minute)</span>
          </h4>
          <div className="bg-bg-secondary rounded-lg p-6">
            {/*  */}
            <div className="text-center mb-6">
              <div className="font-bold text-primary text-4xl mb-1">
                {Math.round(speechRate * 150)}
              </div>
              <div className="text-sm text-text-secondary">WPM</div>
              <div className="text-xs text-text-secondary mt-2">
                {speechRate < 0.75
                  ? ' '
                  : speechRate >= 0.95
                    ? ' '
                    : ' '}
              </div>
            </div>

            {/*  */}
            <div className="relative mb-8">
              <input
                type="range"
                min="0.6"
                max="1.0"
                step="0.05"
                value={speechRate}
                onChange={(e) => handleSpeechRateChange(parseFloat(e.target.value))}
                className="w-full h-3 bg-border-color rounded-lg appearance-none cursor-pointer accent-primary"
                aria-label=""
              />
              {/*  */}
              <div className="absolute top-0 left-0 w-full h-3 pointer-events-none">
                {/* 0.6 (90 WPM) -  (0%) */}
                <div className="absolute top-1/2 left-0 -translate-y-1/2 w-0.5 h-5 bg-gray-400"></div>
                {/* 0.75 (113 WPM) -  (37.5%: (0.75-0.6)/(1.0-0.6) = 0.15/0.4 = 0.375) */}
                <div className="absolute top-1/2 left-[37.5%] -translate-y-1/2 w-0.5 h-5 bg-blue-500"></div>
                {/* 0.9 (135 WPM) -  (75%: (0.9-0.6)/(1.0-0.6) = 0.3/0.4 = 0.75) */}
                <div className="absolute top-1/2 left-[75%] -translate-y-1/2 w-0.5 h-5 bg-green-500"></div>
                {/* 1.0 (150 WPM) -  (100%) */}
                <div className="absolute top-1/2 left-full -translate-y-1/2 -translate-x-0.5 w-0.5 h-5 bg-orange-500"></div>
              </div>
            </div>

            {/*  */}
            <div className="grid grid-cols-4 gap-2 text-center text-xs">
              <button
                onClick={() => handleSpeechRateChange(0.6)}
                className="bg-gray-50/50 rounded-lg p-3 border border-gray-200 hover:bg-gray-100:bg-gray-700/50 transition-colors cursor-pointer"
              >
                <div className="font-bold text-gray-600">90 WPM</div>
                <div className="text-gray-500 mt-1"></div>
              </button>
              <button
                onClick={() => handleSpeechRateChange(0.75)}
                className="bg-blue-50/20 rounded-lg p-3 border border-blue-200 hover:bg-blue-100:bg-blue-800/30 transition-colors cursor-pointer"
              >
                <div className="font-bold text-blue-600">113 WPM</div>
                <div className="text-blue-500 mt-1"></div>
              </button>
              <button
                onClick={() => handleSpeechRateChange(0.9)}
                className="bg-green-50/20 rounded-lg p-3 border border-green-200 hover:bg-green-100:bg-green-800/30 transition-colors cursor-pointer"
              >
                <div className="font-bold text-green-600">135 WPM</div>
                <div className="text-green-500 mt-1"></div>
              </button>
              <button
                onClick={() => handleSpeechRateChange(1.0)}
                className="bg-orange-50/20 rounded-lg p-3 border border-orange-200 hover:bg-orange-100:bg-orange-800/30 transition-colors cursor-pointer"
              >
                <div className="font-bold text-orange-600">150 WPM</div>
                <div className="text-orange-500 mt-1"></div>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="bg-card-bg rounded-xl p-6 shadow-md border-2 border-card-border">
        <h3 className="text-xl font-bold text-text-color mb-4 flex items-center gap-2">
          <span></span>
          <span></span>
        </h3>
        <div className="space-y-3">
          <a
            href="/privacy.html"
            target="_blank"
            rel="noopener noreferrer"
            className="block w-full px-4 py-3 bg-primary text-white text-center rounded-lg font-semibold hover:bg-btn-primary-hover transition-colors shadow-md"
          >
             
          </a>
          <p className="text-sm text-text-secondary bg-bg-secondary px-4 py-3 rounded-lg text-center">
            
          </p>
        </div>
      </div>
    </div>
  );
}

export default SettingsView;
