//  ()
class AnalystCommentGenerator {
  generateCorrectComment(ctx: CommentContext): string {
    const parts: string[] = [];

    parts.push(``);
    parts.push(`: ${(ctx.responseTime / 1000).toFixed(1)}`);

    if (ctx.attemptCount > 1) {
      parts.push(`: ${ctx.attemptCount}`);
    }

    if (ctx.correctStreak > 0) {
      parts.push(`: ${ctx.correctStreak}`);
      const newAccuracy = Math.min(100, ctx.userAccuracy + 2);
      parts.push(`: ${ctx.userAccuracy.toFixed(0)}%  ${newAccuracy.toFixed(0)}%`);
    }

    if (ctx.difficulty === 'advanced') {
      parts.push(`: `);
      parts.push(``);
    }

    if (ctx.isWeakCategory) {
      parts.push(`(${ctx.category})`);
      const newCatAccuracy = Math.min(100, ctx.categoryAccuracy + 3);
      parts.push(
        `: ${ctx.categoryAccuracy.toFixed(0)}%  ${newCatAccuracy.toFixed(0)}%`
      );
    }

    parts.push(``);

    return parts.join(' ');
  }

  generateIncorrectComment(ctx: CommentContext): string {
    const parts: string[] = [];

    parts.push(``);
    parts.push(`: ${ctx.attemptCount}`);

    if (ctx.incorrectStreak > 0) {
      parts.push(`: ${ctx.incorrectStreak}`);
      const newAccuracy = Math.max(0, ctx.userAccuracy - 2);
      parts.push(`: ${ctx.userAccuracy.toFixed(0)}%  ${newAccuracy.toFixed(0)}%`);
    }

    if (ctx.difficulty === 'beginner') {
      parts.push(``);
      parts.push(``);
    }

    if (ctx.hasSeenBefore) {
      parts.push(`: ${ctx.previousAttempts + 1}`);
      parts.push(`: `);
    }

    parts.push(`: 80%`);

    return parts.join(' ');
  }
}

