import { logger } from '@/utils/logger';

export interface PassageMetadata {
  id: string;
  title: string;
  level: 'beginner' | 'intermediate' | 'advanced';
  topic: string;
  wordCount: number;
  filePath: string;
}

export interface LoadedPassage extends PassageMetadata {
  content: string;
  sections: PassageSection[];
}

export interface PassageSection {
  title: string;
  paragraphs: string[];
}

/**
 * 
 * : /data/passages-for-phrase-work/beginner_1200_Shopping-at-the-Supermarket.txt
 *      "Shopping-at-the-Supermarket"
 */
function extractTitleFromPath(filePath: string): string {
  const fileName = filePath.split('/').pop() || '';
  const withoutExt = fileName.replace(/\.txt$/, '');
  const parts = withoutExt.split('_');

  if (parts.length >= 3) {
    // __ 
    const titlePart = parts.slice(2).join('_');
    // 
    return titlePart;
  }

  return fileName;
}

