import { useState } from 'react';
import type { Question, QuizState } from '@/types';

// 
interface SessionStats {
  correct: number;
  incorrect: number;
  review: number;
  mastered: number;
}

/**
 * 
 * - quizState: 
 * - sessionStats: 
 * - reviewFocusMode: 
 * - reviewQuestionPool: 
 * - reviewCorrectStreak: 
 */
export function useQuizState() {
  // 
  const [sessionStats, setSessionStats] = useState<SessionStats>({
    correct: 0,
    incorrect: 0,
    review: 0,
    mastered: 0,
  });

  // 
  const [quizState, setQuizState] = useState<QuizState>({
    questions: [],
    currentIndex: 0,
    score: 0,
    totalAnswered: 0,
    answered: false,
    selectedAnswer: null,
  });

  // 
  const [reviewFocusMode, setReviewFocusMode] = useState<boolean>(false);
  const [reviewQuestionPool, setReviewQuestionPool] = useState<Question[]>([]);
  const [reviewCorrectStreak, setReviewCorrectStreak] = useState<Map<string, number>>(new Map());

  return {
    sessionStats,
    setSessionStats,
    quizState,
    setQuizState,
    reviewFocusMode,
    setReviewFocusMode,
    reviewQuestionPool,
    setReviewQuestionPool,
    reviewCorrectStreak,
    setReviewCorrectStreak,
  };
}
