/**
 * 
 */
export enum BadgeType {
  // 
  FIRST_SESSION = 'first_session', // 
  STREAK_3_DAYS = 'streak_3_days', // 3
  STREAK_7_DAYS = 'streak_7_days', // 1
  STREAK_30_DAYS = 'streak_30_days', // 1
  TOTAL_100_WORDS = 'total_100_words', // 100
  TOTAL_500_WORDS = 'total_500_words', // 500
  TOTAL_1000_WORDS = 'total_1000_words', // 1000

  // 
  PERFECT_SESSION = 'perfect_session', // 
  ACCURACY_90 = 'accuracy_90', // 90%10
  ACCURACY_95 = 'accuracy_95', // 95%10

  // 
  EARLY_BIRD = 'early_bird', // 5
  NIGHT_OWL = 'night_owl', // 5
  SPEED_MASTER = 'speed_master', // 2

  // 
  REVIEW_CHAMPION = 'review_champion', // 50
  MASTER_100 = 'master_100', // 100

  // 
  COMEBACK = 'comeback', // 1
  MARATHON = 'marathon', // 1
  FOCUSED = 'focused', // 10
}

/**
 * 
 */
export interface Badge {
  type: BadgeType;
  name: string;
  description: string;
  icon: string;
  rarity: 'common' | 'rare' | 'epic' | 'legendary';
  earnedAt?: Date;
}

/**
 * 
 */
export interface UserLevel {
  level: number;
  currentXP: number;
  requiredXP: number;
  totalXP: number;
}

/**
 * 
 */
export interface Streak {
  current: number;
  longest: number;
  lastStudyDate: string; // YYYY-MM-DD
}

/**
 * 
 */
export interface Milestone {
  id: string;
  title: string;
  description: string;
  target: number;
  current: number;
  reward: {
    xp: number;
    badge?: BadgeType;
  };
  completed: boolean;
  completedAt?: Date;
}

/**
 * 
 */
export interface Challenge {
  id: string;
  title: string;
  description: string;
  type: 'daily' | 'weekly' | 'monthly';
  startDate: Date;
  endDate: Date;
  target: number;
  progress: number;
  reward: {
    xp: number;
    badge?: BadgeType;
  };
  completed: boolean;
}

/**
 * 
 */
export interface GamificationStats {
  level: UserLevel;
  badges: Badge[];
  streak: Streak;
  milestones: Milestone[];
  challenges: Challenge[];
  totalSessions: number;
  totalWordsLearned: number;
  totalCorrectAnswers: number;
  averageAccuracy: number;
  lastSessionDate?: Date;
}

/**
 * 
 */
export interface Feedback {
  type: 'encouragement' | 'praise' | 'milestone' | 'streak' | 'level_up';
  message: string;
  icon: string;
  priority: number; // 1-5 (5)
}

