/**
 * 
 */
export const XP_PER_LEVEL = (level: number): number => {
  return Math.floor(100 * Math.pow(1.15, level - 1));
};

/**
 * 
 */
export const XP_REWARDS = {
  CORRECT_ANSWER: 10, // 
  PERFECT_SESSION: 100, // 
  FIRST_TRY_CORRECT: 15, // 
  REVIEW_CORRECT: 20, // 
  STREAK_BONUS_PER_DAY: 5, // 1
  SESSION_COMPLETION: 50, // 
  BADGE_EARNED: 200, // 
  CHALLENGE_COMPLETED: 500, // 
};

