import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App.tsx';
import './index-tailwind.css';
import * as Sentry from '@sentry/react';

// 
const initializeDarkMode = () => {
  const saved = localStorage.getItem('darkMode');
  let mode: 'light' | 'dark' | 'system' = 'system';

  if (saved === 'system' || saved === 'light' || saved === 'dark') {
    mode = saved;
  } else if (saved === 'true') {
    // boolean
    mode = 'dark';
    localStorage.setItem('darkMode', 'dark');
  } else if (saved === 'false') {
    mode = 'light';
    localStorage.setItem('darkMode', 'light');
  }

  // Tailwinddark
  let isDark = false;
  if (mode === 'system') {
    isDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
  } else {
    isDark = mode === 'dark';
  }

  // Tailwinddark
  document.documentElement.classList.toggle('dark', isDark);
};

// FLASH
initializeDarkMode();

// Sentry
// DSN
const sentryDsn = import.meta.env.VITE_SENTRY_DSN || '';
if (import.meta.env.PROD && sentryDsn && sentryDsn !== 'your-sentry-dsn-here') {
  Sentry.init({
    dsn: sentryDsn,
    environment: import.meta.env.MODE,

    // 10%
    tracesSampleRate: 0.1,

    // 
    replaysSessionSampleRate: 0,
    replaysOnErrorSampleRate: 1.0,

    integrations: [Sentry.browserTracingIntegration(), Sentry.replayIntegration()],

    // 
    beforeSend(event, _hint) {
      // Sentry
      const msg = event.message || event.exception?.values?.[0]?.value || '';
      if (
        msg.includes('Sentry') ||
        msg.includes('Sentry') ||
        msg.includes(' Sentry')
      ) {
        return null;
      }
      // UIErrorLogPanel/ErrorBadge
      const frames = event.exception?.values?.[0]?.stacktrace?.frames || [];
      if (Array.isArray(frames)) {
        const uiError = frames.some((f: any) => {
          const fn = `${f.function || ''}`;
          const file = `${f.filename || ''}`;
          return (
            fn.includes('ErrorLogPanel') ||
            fn.includes('ErrorBadge') ||
            file.includes('ErrorLogPanel') ||
            file.includes('ErrorBadge')
          );
        });
        if (uiError) return null;
      }
      // LocalStorage
      if (event.exception?.values?.[0]?.value?.includes('localStorage')) {
        return null;
      }

      // ResizeObserver
      if (event.message?.includes('ResizeObserver')) {
        return null;
      }

      // Service Worker
      const errorMsg = event.exception?.values?.[0]?.value || '';
      if (
        errorMsg.includes('newestWorker') ||
        errorMsg.includes('serviceWorker') ||
        errorMsg.includes('InvalidStateError')
      ) {
        return null;
      }

      return event;
    },
  });
}

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
