import { ReadingPassage, ReadingSegment } from '../types';
import { getPassageList, loadPassage, loadOriginalPassage } from './passageLoader';
import { logger } from '@/utils/logger';

// : 
function getLemma(word: string): string {
  const cleaned = word.toLowerCase().trim();

  // -ing
  if (cleaned.endsWith('ing') && cleaned.length > 4) {
    const base = cleaned.slice(0, -3);
    if (base.endsWith('n') || base.endsWith('m') || base.endsWith('t')) {
      return base.slice(0, -1); // running  run
    }
    return base + 'e'; // making  make ()
  }

  // -ed
  if (cleaned.endsWith('ed') && cleaned.length > 3) {
    const base = cleaned.slice(0, -2);
    if (base.endsWith('i')) {
      return base.slice(0, -1) + 'y'; // studied  study
    }
    return base;
  }

  // -s/-es
  if (cleaned.endsWith('es') && cleaned.length > 3) {
    return cleaned.slice(0, -2);
  }
  if (cleaned.endsWith('s') && cleaned.length > 2 && !cleaned.endsWith('ss')) {
    return cleaned.slice(0, -1);
  }

  return cleaned;
}

/**
 * 20
 */
function _splitLongSentence(sentence: string): string[] {
  const words = sentence.trim().split(/\s+/);

  // 20
  if (words.length <= 20) {
    return [sentence];
  }

  // 
  const conjunctionPattern =
    /\b(when|if|because|although|while|since|after|before|unless|until|as|though|whereas)\b/gi;

  // 
  const parts: string[] = [];
  let currentPart = '';
  let wordCount = 0;

  const sentenceWords = sentence.match(/\S+|\s+/g) || [];

  for (let i = 0; i < sentenceWords.length; i++) {
    const token = sentenceWords[i];

    // 
    if (/^\s+$/.test(token)) {
      currentPart += token;
      continue;
    }

    // 20
    if (wordCount >= 15 && conjunctionPattern.test(token)) {
      // 
      if (currentPart.trim()) {
        parts.push(currentPart.trim());
      }
      // 
      currentPart = token;
      wordCount = 1;
    } else {
      currentPart += token;
      wordCount++;
    }
  }

  // 
  if (currentPart.trim()) {
    parts.push(currentPart.trim());
  }

  // 
  return parts.length > 0 ? parts : [sentence];
}

/**
 * 
 */
async function loadFullTranslation(passageId: string): Promise<string> {
  try {
    // passageId
    // : beginner-morning-routine -> /data/passages/passages-translations/beginner_50_Morning-Routine-ja.txt
    // : advanced-environmental-issues -> /data/passages-translations/advanced-environmental-issues-ja.txt

    //  (passages/passages-translations/)
    let translationFilePath = `/data/passages/passages-translations/${passageId}-ja.txt`;

    console.log(`[] Attempting to load: ${translationFilePath} for passageId: ${passageId}`);

    let response = await fetch(translationFilePath);

    // 
    if (!response.ok) {
      translationFilePath = `/data/passages-translations/${passageId}-ja.txt`;
      console.log(`[] Trying old path: ${translationFilePath}`);
      response = await fetch(translationFilePath);
    }

    if (!response.ok) {
      console.log(`[] File not found (${response.status}): ${translationFilePath}`);
      return '';
    }

    const content = await response.text();
    console.log(
      `[] Successfully loaded ${content.length} characters from ${translationFilePath}`
    );
    return content;
  } catch (_error) {
    console.error(`[] Error loading full translation for ${passageId}:`, _error);
    return '';
  }
}

/**
 * passages-for-phrase-work-ja
 * : 
 * passages-for-phrase-work
 */
async function loadJapanesePhrases(passageId: string): Promise<string[]> {
  try {
    // passageId
    const metadata = getPassageList().find((p) => p.id === passageId);
    if (!metadata) return [];

    // 
    const fileName = metadata.filePath.split('/').pop() || '';

    // passages-for-phrase-work-ja
    // : beginner_50_Morning-Routine.txt -> /data/passages-for-phrase-work-ja/beginner_50_Morning-Routine-ja.txt
    const jaFilePath = `/data/passages-for-phrase-work-ja/${fileName.replace(/\.txt$/, '-ja.txt')}`;

    const response = await fetch(jaFilePath);
    if (!response.ok) {
      logger.log(`No Japanese phrase file found: ${jaFilePath}`);
      return [];
    }

    const content = await response.text();
    const lines = content
      .split('\n')
      .map((line) => line.trim())
      .filter((line) => line.length > 0); // 

    logger.log(`Loaded ${lines.length} Japanese phrases from ${jaFilePath}`);
    return lines;
  } catch (_error) {
    logger.error(`Error loading Japanese phrases for ${passageId}:`, _error);
    return [];
  }
}

/**
 * .txt  ReadingPassage 
 */
export async function convertPassageToReadingFormat(
  passageId: string,
  wordDictionary: Map<string, any>
): Promise<ReadingPassage | null> {
  const loaded = await loadPassage(passageId);
  if (!loaded) return null;

  // 
  const [japanesePhrases, fullTranslation, originalText] = await Promise.all([
    loadJapanesePhrases(passageId),
    loadFullTranslation(passageId),
    loadOriginalPassage(passageId),
  ]);
  let japaneseIndex = 0;

  // 
  const phrases: any[] = [];

  loaded.sections.forEach((section) => {
    section.paragraphs.forEach((paragraph) => {
      // Speaker: "..."
      const conversationMatch = paragraph.match(/^([^:]+):\s*"([^"]+)"$/);

      if (conversationMatch) {
        // : 1
        const fullText = paragraph.trim();
        const words = fullText.split(/\s+/);

        // 
        const segments: ReadingSegment[] = words
          .map((word) => {
            // 
            let processWord = word;
            let leadingQuote = '';
            if (processWord.startsWith('"')) {
              leadingQuote = '"';
              processWord = processWord.substring(1);
            }

            // Ms., Mr., Dr.
            const abbreviationPattern =
              /^(Ms|Mr|Mrs|Dr|Prof|St|Ave|Inc|Ltd|etc)\.$|^[A-Z]\.$|^vs\.$|^e\.g\.$|^i\.e\.$/;
            if (abbreviationPattern.test(processWord)) {
              // 1
              const lemma = getLemma(processWord.replace(/\.$/, '')); // 
              const wordData = wordDictionary.get(lemma);
              const meaning = wordData?.meaning || '';

              const result: ReadingSegment[] = [];
              if (leadingQuote) {
                result.push({
                  word: leadingQuote,
                  meaning: '',
                  isUnknown: false,
                });
              }
              result.push({
                word: processWord,
                meaning: meaning === '-' ? '' : meaning,
                isUnknown: false,
              });
              return result;
            }

            // 
            const punctuationMatch = processWord.match(/([.,!?;:"])$/);

            if (punctuationMatch) {
              const cleanWord = processWord.replace(/[.,!?;:"]$/, '');
              const punctuation = punctuationMatch[1];

              if (!cleanWord) {
                // 
                const result: ReadingSegment[] = [];
                if (leadingQuote) {
                  result.push({
                    word: leadingQuote,
                    meaning: '',
                    isUnknown: false,
                  });
                }
                result.push({
                  word: punctuation,
                  meaning: '',
                  isUnknown: false,
                });
                return result;
              }

              const lemma = getLemma(cleanWord);
              const wordData = wordDictionary.get(lemma);
              const meaning = wordData?.meaning || '';

              const result: ReadingSegment[] = [];
              if (leadingQuote) {
                result.push({
                  word: leadingQuote,
                  meaning: '',
                  isUnknown: false,
                });
              }
              result.push({
                word: cleanWord,
                meaning: meaning === '-' ? '' : meaning,
                isUnknown: false,
              });
              result.push({
                word: punctuation,
                meaning: '',
                isUnknown: false,
              });
              return result;
            } else {
              // 
              const lemma = getLemma(processWord);
              const wordData = wordDictionary.get(lemma);
              const meaning = wordData?.meaning || '';

              const result: ReadingSegment[] = [];
              if (leadingQuote) {
                result.push({
                  word: leadingQuote,
                  meaning: '',
                  isUnknown: false,
                });
              }
              result.push({
                word: processWord,
                meaning: meaning === '-' ? '' : meaning,
                isUnknown: false,
              });
              return result;
            }
          })
          .flat();

        // 
        const japanese = japanesePhrases[japaneseIndex] || '';
        japaneseIndex++;

        phrases.push({
          english: fullText,
          japanese: japanese,
          phraseMeaning: japanese, // phraseMeaning
          words: words,
          segments: segments,
        });
      } else {
        // passages-for-phrase-work
        // 1
        const phraseText = paragraph.trim();

        // 
        const words = phraseText.split(/\s+/);

        // 
        const segments: ReadingSegment[] = words
          .map((word) => {
            // Ms., Mr., Dr.
            const abbreviationPattern =
              /^(Ms|Mr|Mrs|Dr|Prof|St|Ave|Inc|Ltd|etc)\.$|^[A-Z]\.$|^vs\.$|^e\.g\.$|^i\.e\.$/;
            if (abbreviationPattern.test(word)) {
              // 1
              const lemma = getLemma(word.replace(/\.$/, '')); // 
              const wordData = wordDictionary.get(lemma);
              const meaning = wordData?.meaning || '';
              return {
                word: word,
                meaning: meaning === '-' ? '' : meaning,
                isUnknown: false,
              };
            }

            // 
            const punctuationMatch = word.match(/([.,!?;:])$/);

            if (punctuationMatch) {
              const cleanWord = word.replace(/[.,!?;:]$/, '');
              const punctuation = punctuationMatch[1];

              // 
              if (!cleanWord) {
                return {
                  word: punctuation,
                  meaning: '',
                  isUnknown: false,
                };
              }

              const lemma = getLemma(cleanWord);
              const wordData = wordDictionary.get(lemma);
              const meaning = wordData?.meaning || '';

              return [
                {
                  word: cleanWord,
                  meaning: meaning === '-' ? '' : meaning,
                  isUnknown: false,
                },
                {
                  word: punctuation,
                  meaning: '',
                  isUnknown: false,
                },
              ];
            } else {
              // 
              const lemma = getLemma(word);
              const wordData = wordDictionary.get(lemma);
              const meaning = wordData?.meaning || '';

              return {
                word: word,
                meaning: meaning === '-' ? '' : meaning,
                isUnknown: false,
              };
            }
          })
          .flat();

        // 
        const japanese = japanesePhrases[japaneseIndex] || '';
        japaneseIndex++;

        phrases.push({
          english: phraseText,
          japanese: japanese,
          phraseMeaning: japanese, // phraseMeaning
          words: words,
          segments: segments,
        });
      }
    });
  });

  // ReadingPassage
  const readingPassage: ReadingPassage = {
    id: loaded.id,
    title: loaded.title,
    level: loaded.level,
    actualWordCount: loaded.wordCount,
    phrases: phrases,
    translation: fullTranslation, // 
    originalText: originalText, // 
  };

  return readingPassage;
}

/**
 * JSON
 * public/data/passages-phrase-learning/*.json
 */
export async function loadPhraseLearningJSON(passageId: string): Promise<ReadingPassage | null> {
  try {
    const response = await fetch(`/data/passages-phrase-learning/${passageId}.json`);
    if (!response.ok) {
      logger.log(`No phrase learning JSON found for ${passageId}, will use .txt conversion`);
      return null; // null
    }

    const data = await response.json();
    logger.log(
      `Loaded phrase learning JSON for ${passageId}, phrases: ${data.phrases?.length || 0}`
    );

    // 
    const [fullTranslation, originalText] = await Promise.all([
      loadFullTranslation(passageId),
      loadOriginalPassage(passageId),
    ]);

    // JSON(ReadingPassage)
    const readingPassage: ReadingPassage = {
      ...data,
      phrases: data.phrases || [], // phrases
      translation: fullTranslation, // 
      originalText: originalText, // 
    };

    return readingPassage;
  } catch {
    // old
    // SyntaxErrorJSON404
    logger.log(
      `Skipping phrase learning JSON for ${passageId} (file may be old or moved), will use .txt conversion`
    );
    return null;
  }
}

/**
 *  ReadingPassage[] 
 * : 1) JSON, 2) .txt
 */
export async function loadAllPassagesAsReadingFormat(
  wordDictionary: Map<string, any>
): Promise<ReadingPassage[]> {
  const metadata = getPassageList();
  const passages: ReadingPassage[] = [];

  logger.log(`Loading ${metadata.length} passages...`);

  for (const meta of metadata) {
    // JSON
    let passage = await loadPhraseLearningJSON(meta.id);

    // JSON.txt
    if (!passage) {
      passage = await convertPassageToReadingFormat(meta.id, wordDictionary);
    }

    if (passage) {
      logger.log(` Loaded passage: ${meta.id} (${passage.phrases?.length || 0} phrases)`);
      passages.push(passage);
    } else {
      logger.error(` Failed to load passage: ${meta.id}`);
    }
  }

  logger.log(`Total passages loaded: ${passages.length}`);
  return passages;
}

/**
 * 'beginner'  ''
 */
export function getLevelLabel(level: string): string {
  const levelMap: Record<string, string> = {
    beginner: '',
    intermediate: '',
    advanced: '',
  };
  return levelMap[level] || level;
}
