/**
 * 
 * S, V, O, C, M
 */

export type GrammarTag =
  | 'S'
  | 'V'
  | 'O'
  | 'C'
  | 'M'
  | 'Prep'
  | 'Conj'
  | 'Det'
  | 'Adj'
  | 'Adv'
  | 'Unknown';

export interface GrammarAnalysisResult {
  word: string;
  tag: GrammarTag;
  color: string;
  description: string;
}

