/**
 * 
 */
function getTagColor(tag: GrammarTag): string {
  const colorMap: Record<GrammarTag, string> = {
    S: '#3b82f6', //  - 
    V: '#ef4444', //  - 
    O: '#10b981', //  - 
    C: '#f59e0b', //  - 
    M: '#8b5cf6', //  - 
    Prep: '#6366f1', //  - 
    Conj: '#ec4899', //  - 
    Det: '#14b8a6', //  - 
    Adj: '#f97316', //  - 
    Adv: '#a855f7', //  - 
    Unknown: '#6b7280', //  - 
  };
  return colorMap[tag];
}

/**
 * 
 */
function getTagDescription(tag: GrammarTag): string {
  const descMap: Record<GrammarTag, string> = {
    S: '',
    V: '',
    O: '',
    C: '',
    M: '',
    Prep: '',
    Conj: '',
    Det: '',
    Adj: '',
    Adv: '',
    Unknown: '',
  };
  return descMap[tag];
}

/**
 * 
 */
function classifyWord(word: string, index: number, words: string[]): GrammarTag {
  const lower = word.toLowerCase();

  // 
  if (/^[.,!?;:]$/.test(word)) {
    return 'Unknown';
  }

  // 
  if (VERBS.has(lower)) {
    return 'V';
  }

  // 
  if (PREPOSITIONS.has(lower)) {
    return 'Prep';
  }

  // 
  if (CONJUNCTIONS.has(lower)) {
    return 'Conj';
  }

  // 
  if (DETERMINERS.has(lower)) {
    return 'Det';
  }

  // 
  if (ADJECTIVES.has(lower)) {
    return 'Adj';
  }

  // 
  if (ADVERBS.has(lower)) {
    return 'Adv';
  }

  // 
  if (PRONOUNS.has(lower)) {
    // 
    if (index === 0 || (index > 0 && VERBS.has(words[index + 1]?.toLowerCase()))) {
      return 'S';
    }
    // 
    return 'O';
  }

  return 'Unknown';
}

/**
 * 
 */
function getPunctuationMeaning(punctuation: string): string {
  const meanings: Record<string, string> = {
    '.': '',
    ',': '',
    '!': '',
    '?': '',
    ';': '',
    ':': '',
    '-': '',
    '': '',
    '': '',
    '"': '',
    "'": '',
    '(': '',
    ')': '',
  };
  return meanings[punctuation] || '';
}

/**
 * 
 */
export function analyzeSentence(sentence: string): GrammarAnalysisResult[] {
  // 
  const words = sentence.match(/\b[\w']+\b|[.,!?;:\-"'()]/g) || [];

  const results: GrammarAnalysisResult[] = [];
  let foundVerb = false;
  let foundSubject = false;

  for (let i = 0; i < words.length; i++) {
    const word = words[i];

    // 
    if (/^[.,!?;:\-"'()]$/.test(word)) {
      results.push({
        word,
        tag: 'Unknown',
        color: '#6b7280',
        description: getPunctuationMeaning(word),
      });
      continue;
    }

    let tag = classifyWord(word, i, words);

    // 
    if (tag === 'Unknown') {
      // 
      if (i === 0 && /^[A-Z]/.test(word) && !foundSubject) {
        tag = 'S';
        foundSubject = true;
      }
      // 
      else if (foundVerb && !foundSubject) {
        tag = 'O';
      }
      // 
      else if (i > 0 && PREPOSITIONS.has(words[i - 1].toLowerCase())) {
        tag = 'M';
      }
      // 
      else {
        tag = 'M';
      }
    }

    if (tag === 'V') {
      foundVerb = true;
    }
    if (tag === 'S') {
      foundSubject = true;
    }

    results.push({
      word,
      tag,
      color: getTagColor(tag),
      description: getTagDescription(tag),
    });
  }

  return results;
}

/**
 * 
 */
export function getGrammarStats(results: GrammarAnalysisResult[]): Record<GrammarTag, number> {
  const stats: Record<string, number> = {};

  results.forEach((result) => {
    stats[result.tag] = (stats[result.tag] || 0) + 1;
  });

  return stats as Record<GrammarTag, number>;
}

/**
 * 
 * 
 */
export interface PhrasalExpression {
  words: string[];
  meaning: string;
  type: 'phrasal-verb' | 'idiom' | 'time-expression' | 'determiner-noun';
}

const PHRASAL_EXPRESSIONS: PhrasalExpression[] = [
  //  (Phrasal Verbs) - 
  { words: ['wake', 'up'], meaning: '', type: 'phrasal-verb' },
  { words: ['get', 'up'], meaning: '', type: 'phrasal-verb' },
  { words: ['wake', 'up'], meaning: '', type: 'phrasal-verb' },
  { words: ['brush', 'my', 'teeth'], meaning: '', type: 'phrasal-verb' },
  { words: ['wash', 'my', 'face'], meaning: '', type: 'phrasal-verb' },
  { words: ['have', 'breakfast'], meaning: '', type: 'phrasal-verb' },
  { words: ['go', 'to', 'school'], meaning: '', type: 'phrasal-verb' },
  { words: ['come', 'back'], meaning: '', type: 'phrasal-verb' },
  { words: ['come', 'home'], meaning: '', type: 'phrasal-verb' },
  { words: ['do', 'homework'], meaning: '', type: 'phrasal-verb' },
  { words: ['go', 'to', 'bed'], meaning: '', type: 'phrasal-verb' },

  // 
  { words: ['at', 'seven'], meaning: '7', type: 'time-expression' },
  { words: ['in', 'the', 'morning'], meaning: '', type: 'time-expression' },
  { words: ['in', 'the', 'afternoon'], meaning: '', type: 'time-expression' },
  { words: ['in', 'the', 'evening'], meaning: '', type: 'time-expression' },
  { words: ['at', 'night'], meaning: '', type: 'time-expression' },

  // +
  { words: ['every', 'morning'], meaning: '', type: 'determiner-noun' },
  { words: ['every', 'day'], meaning: '', type: 'determiner-noun' },
  { words: ['every', 'night'], meaning: '', type: 'determiner-noun' },
  { words: ['every', 'week'], meaning: '', type: 'determiner-noun' },
];

/**
 * 
 */
export function detectPhrasalExpressions(words: string[]): PhrasalExpression[] {
  const detected: PhrasalExpression[] = [];
  const lowerWords = words.map((w) => w.toLowerCase());

  for (const expression of PHRASAL_EXPRESSIONS) {
    const exprLower = expression.words.map((w) => w.toLowerCase());

    // 
    for (let i = 0; i <= lowerWords.length - exprLower.length; i++) {
      let match = true;
      for (let j = 0; j < exprLower.length; j++) {
        if (lowerWords[i + j] !== exprLower[j]) {
          match = false;
          break;
        }
      }

      if (match) {
        detected.push({
          ...expression,
          words: words.slice(i, i + exprLower.length), // 
        });
      }
    }
  }

  return detected;
}

/**
 * 
 */
export interface GrammarPattern {
  name: string;
  meaning: string;
  pattern: RegExp;
  explanation: string;
}

const GRAMMAR_PATTERNS: GrammarPattern[] = [
  {
    name: 'too ~ to ...',
    meaning: '...',
    pattern: /\btoo\s+\w+\s+to\s+\w+/i,
    explanation: 'too + / + to + ...',
  },
  {
    name: 'so ~ that ...',
    meaning: '...',
    pattern: /\bso\s+\w+\s+that\b/i,
    explanation: 'so + / + that ~...',
  },
  {
    name: 'so that ...',
    meaning: '',
    pattern: /\bso\s+that\b/i,
    explanation: 'so that ~',
  },
  {
    name: 'It is ~ for ... to',
    meaning: '...',
    pattern: /\bit\s+is\s+\w+\s+for\s+\w+\s+to\b/i,
    explanation:
      'It is +  + for +  + to + ()...',
  },
  {
    name: 'It is ~ to ...',
    meaning: '...',
    pattern: /\bit\s+is\s+\w+\s+to\s+\w+/i,
    explanation: 'It is +  + to + ...',
  },
  {
    name: 'It is ~ that ...',
    meaning: '... ()',
    pattern: /\bit\s+is\s+\w+\s+that\b/i,
    explanation: 'It is ~ that ...',
  },
  {
    name: 'not only ~ but also ...',
    meaning: '...',
    pattern: /\bnot\s+only\s+.+\s+but\s+also\b/i,
    explanation: 'not only A but also BAB',
  },
  {
    name: 'either ~ or ...',
    meaning: '...',
    pattern: /\beither\s+.+\s+or\b/i,
    explanation: 'either A or BAB',
  },
  {
    name: 'neither ~ nor ...',
    meaning: '...',
    pattern: /\bneither\s+.+\s+nor\b/i,
    explanation: 'neither A nor BAB',
  },
  {
    name: 'both ~ and ...',
    meaning: '...',
    pattern: /\bboth\s+.+\s+and\b/i,
    explanation: 'both A and BAB',
  },
  {
    name: 'as ~ as ...',
    meaning: '...',
    pattern: /\bas\s+\w+\s+as\b/i,
    explanation: 'as + / + as ......',
  },
  {
    name: 'not as ~ as ...',
    meaning: '...',
    pattern: /\bnot\s+as\s+\w+\s+as\b/i,
    explanation: 'not as + / + as ......',
  },
  {
    name: 'one of the ~est',
    meaning: '1',
    pattern: /\bone\s+of\s+the\s+\w+est\b/i,
    explanation: 'one of the +  + 1',
  },
  {
    name: 'make/let/have +  + ',
    meaning: '',
    pattern: /\b(make|let|have|help)\s+\w+\s+\w+/i,
    explanation: 'make/let/have +  + ',
  },
  {
    name: 'be used to ~ing',
    meaning: '',
    pattern: /\b(am|is|are|was|were)\s+used\s+to\s+\w+ing\b/i,
    explanation: 'be used to + ',
  },
  {
    name: 'used to + ',
    meaning: '',
    pattern: /\bused\s+to\s+\w+/i,
    explanation: 'used to + ',
  },
];

/**
 * 
 */
export function detectGrammarPatterns(sentence: string): GrammarPattern[] {
  const detected: GrammarPattern[] = [];

  for (const pattern of GRAMMAR_PATTERNS) {
    if (pattern.pattern.test(sentence)) {
      detected.push(pattern);
    }
  }

  return detected;
}
