// LocalStorage
function checkLocalStorageSize() {
  try {
    let totalSize = 0;
    const details: { key: string; size: number }[] = [];

    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key) {
        const value = localStorage.getItem(key);
        if (value) {
          const size = new Blob([value]).size;
          totalSize += size;
          details.push({ key, size });
        }
      }
    }

    const totalMB = totalSize / (1024 * 1024);
    logger.log(` LocalStorage: ${totalMB.toFixed(2)}MB`);

    // 
    details.sort((a, b) => b.size - a.size);
    details.slice(0, 5).forEach((d) => {
      const sizeMB = d.size / (1024 * 1024);
      logger.log(`  - ${d.key}: ${sizeMB.toFixed(2)}MB`);
    });

    // 4MB
    if (totalMB > 4) {
      logger.warn(' LocalStorage');
      // 
      loadProgress();
      logger.log('');
    }
  } catch (error) {
    logger.error('LocalStorage:', error);
  }
}

