// 
import { AIPersonality } from '@/types';

export interface TeacherInteraction {
  mainTeacher: AIPersonality;
  otherTeacher?: AIPersonality;
  message: string;
  type: 'joke' | 'banter' | 'passerby' | 'argument' | 'support';
}

// 10%
export function generateTeacherInteraction(
  currentPersonality: AIPersonality,
  isCorrect: boolean,
  streak: number
): TeacherInteraction | null {
  // 10%
  if (Math.random() > 0.1) {
    return null;
  }

  const interactions = getInteractionPatterns(currentPersonality, isCorrect, streak);
  if (interactions.length === 0) return null;

  return interactions[Math.floor(Math.random() * interactions.length)];
}

// 
function getInteractionPatterns(
  mainTeacher: AIPersonality,
  isCorrect: boolean,
  streak: number
): TeacherInteraction[] {
  const patterns: TeacherInteraction[] = [];

  // 
  if (mainTeacher === 'drill-sergeant') {
    if (isCorrect && streak >= 5) {
      patterns.push({
        mainTeacher: 'drill-sergeant',
        otherTeacher: 'kind-teacher',
        message:
          ' ... ',
        type: 'banter',
      });
      patterns.push({
        mainTeacher: 'drill-sergeant',
        otherTeacher: 'wise-sage',
        message: '  ...',
        type: 'banter',
      });
    }

    if (!isCorrect) {
      patterns.push({
        mainTeacher: 'drill-sergeant',
        otherTeacher: 'kind-teacher',
        message:
          '...  ',
        type: 'argument',
      });
      patterns.push({
        mainTeacher: 'drill-sergeant',
        otherTeacher: 'analyst',
        message:
          '  ',
        type: 'argument',
      });
    }

    // 
    patterns.push({
      mainTeacher: 'drill-sergeant',
      otherTeacher: 'enthusiastic-coach',
      message: ' ...',
      type: 'passerby',
    });
  }

  // 
  if (mainTeacher === 'kind-teacher') {
    if (isCorrect) {
      patterns.push({
        mainTeacher: 'kind-teacher',
        otherTeacher: 'drill-sergeant',
        message:
          '  ...',
        type: 'passerby',
      });
      patterns.push({
        mainTeacher: 'kind-teacher',
        otherTeacher: 'wise-sage',
        message:
          '  ',
        type: 'support',
      });
    }

    if (!isCorrect) {
      patterns.push({
        mainTeacher: 'kind-teacher',
        otherTeacher: 'enthusiastic-coach',
        message:
          '  ',
        type: 'support',
      });
    }

    // 
    patterns.push({
      mainTeacher: 'kind-teacher',
      message:
        '...  ',
      type: 'joke',
    });
  }

  // 
  if (mainTeacher === 'analyst') {
    if (streak >= 10) {
      patterns.push({
        mainTeacher: 'analyst',
        otherTeacher: 'enthusiastic-coach',
        message:
          '10  ...',
        type: 'banter',
      });
    }

    patterns.push({
      mainTeacher: 'analyst',
      otherTeacher: 'drill-sergeant',
      message:
        '...   ',
      type: 'argument',
    });

    patterns.push({
      mainTeacher: 'analyst',
      otherTeacher: 'kind-teacher',
      message:
        '73.4% ... ',
      type: 'banter',
    });

    // 
    patterns.push({
      mainTeacher: 'analyst',
      message:
        '......  ',
      type: 'joke',
    });
  }

  // 
  if (mainTeacher === 'enthusiastic-coach') {
    if (isCorrect && streak >= 5) {
      patterns.push({
        mainTeacher: 'enthusiastic-coach',
        otherTeacher: 'wise-sage',
        message:
          ' ... ',
        type: 'banter',
      });
      patterns.push({
        mainTeacher: 'enthusiastic-coach',
        otherTeacher: 'drill-sergeant',
        message:
          ' ... ',
        type: 'support',
      });
    }

    if (!isCorrect) {
      patterns.push({
        mainTeacher: 'enthusiastic-coach',
        otherTeacher: 'kind-teacher',
        message: '  ',
        type: 'support',
      });
    }

    // 
    patterns.push({
      mainTeacher: 'enthusiastic-coach',
      message: '...  ',
      type: 'joke',
    });
  }

  // 
  if (mainTeacher === 'wise-sage') {
    if (isCorrect) {
      patterns.push({
        mainTeacher: 'wise-sage',
        otherTeacher: 'drill-sergeant',
        message:
          '  ...',
        type: 'banter',
      });
    }

    patterns.push({
      mainTeacher: 'wise-sage',
      otherTeacher: 'analyst',
      message: '  ...',
      type: 'argument',
    });

    patterns.push({
      mainTeacher: 'wise-sage',
      otherTeacher: 'enthusiastic-coach',
      message:
        '...  ...',
      type: 'banter',
    });

    // 
    patterns.push({
      mainTeacher: 'wise-sage',
      message: '...  ...',
      type: 'joke',
    });
  }

  // 
  const commonPatterns: TeacherInteraction[] = [
    {
      mainTeacher: mainTeacher,
      message:
        '    ...',
      type: 'joke',
    },
    {
      mainTeacher: mainTeacher,
      message:
        '... ...  ',
      type: 'joke',
    },
    {
      mainTeacher: mainTeacher,
      message:
        '... IT   ',
      type: 'joke',
    },
    {
      mainTeacher: mainTeacher,
      message:
        '    ',
      type: 'banter',
    },
  ];

  // 5%
  if (Math.random() < 0.05) {
    patterns.push(...commonPatterns);
  }

  return patterns;
}

// 
export function getTimeBasedTeacherChat(): string | null {
  const hour = new Date().getHours();

  // 5%
  if (Math.random() > 0.05) return null;

  if (hour >= 6 && hour < 9) {
    const morning = [
      '  ',
      ' 18 ',
      ' 10...',
    ];
    return morning[Math.floor(Math.random() * morning.length)];
  }

  if (hour >= 12 && hour < 13) {
    const lunch = [
      '  30',
      '  ',
      ' ... ',
    ];
    return lunch[Math.floor(Math.random() * lunch.length)];
  }

  if (hour >= 17 && hour < 19) {
    const evening = [
      '  ',
      ' 1732 ',
      '   ',
    ];
    return evening[Math.floor(Math.random() * evening.length)];
  }

  if (hour >= 22) {
    const lateNight = [
      '...  ',
      '22 ',
      '  ...',
    ];
    return lateNight[Math.floor(Math.random() * lateNight.length)];
  }

  return null;
}

// 
export function getTeacherReactionToStreak(streak: number): string | null {
  if (streak === 10) {
    return '10    ';
  }

  if (streak === 20) {
    return '20...    ...';
  }

  if (streak === 50) {
    return '50';
  }

  return null;
}

// 
export function getSpecialDayChat(): string | null {
  const now = new Date();
  const month = now.getMonth() + 1;
  const date = now.getDate();
  const day = now.getDay();

  // 
  if (day === 1) {
    return '  ';
  }

  // 
  if (day === 5) {
    return '  ';
  }

  // 
  if (month === 12 && date === 25) {
    return '   ';
  }

  // 
  if (month === 1 && date === 1) {
    return '';
  }

  return null;
}
