/**
 * Web Speech API 
 */

import { logger } from '@/utils/logger';

/**
 * 
 * : 'Tom: "Hello, how are you?"'  'Hello, how are you?'
 * @param text 
 * @returns 
 */
export function cleanDialogueText(text: string): string {
  // : ": "..." 
  // 1: Speaker: "text"  text
  // 2: Speaker: text  text

  // ": "..." 
  const quotedPattern = /^[^:]+:\s*[""](.+?)[""]$/;
  const quotedMatch = text.match(quotedPattern);
  if (quotedMatch) {
    return quotedMatch[1].trim();
  }

  // ": text 
  const unquotedPattern = /^[^:]+:\s*(.+)$/;
  const unquotedMatch = text.match(unquotedPattern);
  if (unquotedMatch) {
    return unquotedMatch[1].trim();
  }

  // 
  return text;
}

/**
 * 
 * @param text 
 * @param options 
 */
export function speakEnglish(
  text: string,
  options: {
    rate?: number; //  (0.1 - 10, : 1)
    pitch?: number; //  (0 - 2, : 1)
    volume?: number; //  (0 - 1, : 1)
    lang?: string; //  (: 'en-US')
  } = {}
): void {
  // Web Speech API 
  if (!('speechSynthesis' in window)) {
    logger.warn('Web Speech API');
    return;
  }

  // 
  const cleanedText = cleanDialogueText(text);

  // 
  // speaking/pending
  if (window.speechSynthesis.speaking || window.speechSynthesis.pending) {
    window.speechSynthesis.cancel();
    // 
    setTimeout(() => {
      startSpeech(cleanedText, options);
    }, 50);
    return;
  }

  startSpeech(cleanedText, options);
}

/**
 * 
 */
function startSpeech(
  cleanedText: string,
  options: {
    rate?: number;
    pitch?: number;
    volume?: number;
    lang?: string;
  }
): void {
  // 
  const utterance = new SpeechSynthesisUtterance(cleanedText);

  // localStorage
  const savedRate = localStorage.getItem('speechRate');
  const savedGender = localStorage.getItem('voiceGender');

  // 
  utterance.lang = options.lang || 'en-US';
  utterance.rate = options.rate || (savedRate ? parseFloat(savedRate) : 0.85);
  utterance.pitch = options.pitch || 1.0;
  utterance.volume = options.volume || 1.0;

  //  - 
  const voices = window.speechSynthesis.getVoices();
  const englishVoices = voices.filter((voice) => voice.lang.startsWith('en-'));

  // 
  const highQualityVoices = [
    'Google US English',
    'Google UK English Female',
    'Google UK English Male',
    'Samantha', // macOS
    'Alex', // macOS
    'Microsoft Aria Online',
    'Microsoft Guy Online',
    'Karen', // macOS
    'Daniel', // macOS
  ];

  if (savedGender === 'female') {
    // 
    const femaleVoice =
      englishVoices.find((v) => highQualityVoices.some((hq) => v.name.includes(hq))) ||
      englishVoices.find(
        (v) =>
          v.name.toLowerCase().includes('female') ||
          v.name.toLowerCase().includes('woman') ||
          v.name.toLowerCase().includes('samantha') ||
          v.name.toLowerCase().includes('karen') ||
          v.name.toLowerCase().includes('aria')
      );
    if (femaleVoice) utterance.voice = femaleVoice;
  } else if (savedGender === 'male') {
    // 
    const maleVoice =
      englishVoices.find((v) => highQualityVoices.some((hq) => v.name.includes(hq))) ||
      englishVoices.find(
        (v) =>
          v.name.toLowerCase().includes('male') ||
          v.name.toLowerCase().includes('man') ||
          v.name.toLowerCase().includes('alex') ||
          v.name.toLowerCase().includes('daniel') ||
          v.name.toLowerCase().includes('guy')
      );
    if (maleVoice) utterance.voice = maleVoice;
  } else {
    // 
    const bestVoice = englishVoices.find((v) =>
      highQualityVoices.some((hq) => v.name.includes(hq))
    );
    if (bestVoice) utterance.voice = bestVoice;
  }

  // 
  utterance.onerror = (event) => {
    logger.error(':', event);
  };

  // 
  window.speechSynthesis.speak(utterance);
}

/**
 * 
 */
export function isSpeechSynthesisSupported(): boolean {
  return 'speechSynthesis' in window;
}

/**
 * 
 */
export function stopSpeaking(): void {
  if ('speechSynthesis' in window) {
    window.speechSynthesis.cancel();
  }
}

/**
 * 
 */
export function pauseSpeaking(): void {
  if ('speechSynthesis' in window && window.speechSynthesis.speaking) {
    window.speechSynthesis.pause();
  }
}

/**
 * 
 */
export function resumeSpeaking(): void {
  if ('speechSynthesis' in window && window.speechSynthesis.paused) {
    window.speechSynthesis.resume();
  }
}

/**
 * 
 */
export function isSpeaking(): boolean {
  return 'speechSynthesis' in window && window.speechSynthesis.speaking;
}

/**
 * 
 */
export function isPaused(): boolean {
  return 'speechSynthesis' in window && window.speechSynthesis.paused;
}

/**
 * 
 */
export function getEnglishVoices(): SpeechSynthesisVoice[] {
  if (!isSpeechSynthesisSupported()) {
    return [];
  }

  const voices = window.speechSynthesis.getVoices();
  return voices.filter((voice) => voice.lang.startsWith('en-'));
}

/**
 * 
 * @param text 
 * @param element 
 */
export function speakWordWithFeedback(text: string, element?: HTMLElement): void {
  // 
  speakEnglish(text);

  // 
  if (element) {
    element.classList.add('speaking');

    // 
    setTimeout(() => {
      element.classList.remove('speaking');
    }, 600);
  }
}
